.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.b{
  background-color:rgba(23, 36, 217, 0.5) ;
  margin-top: 200px;
  border-radius: 15px;
  width: 50%;

  margin: auto;
}
.v{
  padding-top: 200px;
}

@media(200px <= width <= 380px){
  .c{
    width: 227px;
  }  
  .b{
    margin: 0%;
    margin-left: 20px;
    margin: 0px;
    width: 320px;
  }
}